<template>
    <div class="content recharge-content">
        <Header title="钻石充值"></Header>
        <div class="recharge_body">
            <div class="recharge" v-for="item in diamond" @click="infoPayment(item)">
                <div class="recharge_left">
                    <img class="diamond-icon" src="../../static/image/wd.png">
                    <span >{{item.money}}</span>
                </div>
                <div class="recharge_center">
                    <div>{{item.desc}}</div>
                </div>
                <div class="recharge_right">
                    &yen; {{item.price.toFixed(2)}}
                </div>
            </div>
        </div>
        <confirm-modal :show="payShow" @confirm_modal="chargePay" @closeModal="payShow = false" :message="payText"></confirm-modal>
    	<mt-popup v-model="failShow" :closeOnClickModal="false" position="top" class="pay-fail-tip" :class="{'pay-success': isSuccess}">
	      <p v-html="failText"></p>
	    </mt-popup>
    </div>
</template>

<script>
import { Toast} from 'mint-ui'
export default {
  data () {
    return {
        diamond:[],
        payShow: false,
        payText: '',
        currPayId: null,
        currPayPrice: null,
        
        failShow: false,
        failText: '<img src="../../static/image/fail-pay.png" />支付失败',
        isSuccess: false
        
    }
  },
  created(){
    this.$api.chargeTextList({
    	platform: window.OPEN_DATA.platform
    }).then(res => {
        this.diamond = res.data
    }, err => {
    	
    })
  },
  methods: {
    infoPayment(item){
    	this.payShow = true
    	this.currPayId = item.itemId
    	this.currPayPrice = parseFloat(item.price)
    	this.payText = '充值 <b style="color: #000;">' + item.price + '</b> 元获得<b style="color: #000;"> ' + item.money + ' </b>钻石'
    	
    },
    pay() {
    	const self = this
    	window.getOpenKey(function(msg){
    		self.$api.chargePay({
	    		openid: msg.data.openid,
	    		openkey: msg.data.openkey,
	    		platform: window.OPEN_DATA.platform,
	    		itemId: self.currPayId
	    	}).then(res => {
				if(res.errCode == 0) {
					self.isSuccess = true
					self.payTip('<img src="../../static/image/succ-pay.png" />充值成功')
					setTimeout(() => {
						window.history.back()
					}, 2000)
				}else if(res.errCode == 44444) {
					window.popPayTips({
					    defaultScore : parseInt(self.currPayPrice * 10),
					    appid : 1106587346
					})
				}
		    }, err => {
		    	
		    })
		    
		})
    },
    chargePay() {
    	const self = this
    	this.pay()
    	
    	window.__paySuccess = function(){
		    self.pay()
		}
		
		window.__payError = function(){
			self.isSuccess = false
			self.payTip('<img src="../../static/image/fail-pay.png" />充值失败')
		}
	    window.__payClose = function(){
	    	self.isSuccess = false
		    self.payTip('<img src="../../static/image/fail-pay.png" />用户取消充值')
		}	
    },
    payTip(text) {
    	this.failShow = true
		this.failText = text
		setTimeout(() => {
			this.failShow = false
		}, 3000)
    }
  }
}
</script>

<style lang="scss" scoped>
.recharge_body{
    width: 100%;
    position: absolute;
    left: 0;
    top: 0.85rem;
    bottom: 0;
    padding: .4rem .2rem;

}
.recharge{
    width: 100%;
    height: 1rem;
    display: flex;
    padding: 0 .3rem;
    align-items: center;
    margin-bottom: .3rem;
    background: url('../../static/image/333.png') no-repeat;
    background-size: 100% 100%;
}
.recharge_left{
    display: flex;
    align-items: center;
    font-size: .33rem;
    color: #fff;
    font-size: .32rem;
    width: 2rem;
    text-shadow: 1px 0 0 #000,0 1px 0 #000,-1px 0 0 #000,0 -1px 0 #000;
    .diamond-icon{
    	width: 0.3rem;
    	margin-right: 0.2rem;
    }
    
}
.recharge_center{
    color: #956134;
    font-size: .28rem;
    font-weight: 500;
    flex: 1;
    overflow: hidden;
}
.recharge_right{
    color: #fc7298;
    height: 0.54rem;
    line-height: 0.54rem;
    border-radius: 0.54rem;
    width: 1.8rem;
    text-align: center;
    background: #fff;
    font-size: 0.3rem;
    font-weight: 700;
}



</style>



